<template>
	<view class="news_detail">
		<text class="title">{{detail.title}}</text>
		<view class="info">
			<text>发表时间：{{detail.add_time|formatDate}}</text>
			<text>浏览：{{detail.click}}</text>
		</view>
		<view class="content">
			<rich-text :nodes="detail.content"></rich-text>
		</view>
	</view>
</template>

<script>
  import {
    getDetail
  } from '@/util/api/news-detail.js';
	export default {
		data() {
			return {
				id: 0,
				detail: {}
			}
		},
		methods: {
			// async getNewsDetail () {
			// 	const res = await this.$myRuquest({
			// 		url: '/api/getnew/'+this.id
			// 	})
			// 	console.log(res)
			// 	this.detail = res.data.message[0]
			// }
      getNewsDetail(){
        let opt = {
          url:`/${this.id}`
        };
        getDetail(opt).then((res) => {
          this.detail = res.data.message[0];
        }).catch((err) => {
          console.log(`err`, err)
        })
      }
		},
		onLoad(options){
			this.id = options.id
      console.log(this.getNewsDetail)
			this.getNewsDetail()
		}
	}
</script>

